import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { IntroPage } from '../pages/intro/intro';



import * as firebase from 'firebase';

var config = {
    apiKey: "AIzaSyD1W8sSKGaeke6FNFNDlrBcpNeaFkC_-Qg",
    authDomain: "ionic-chat-starter-9704d.firebaseapp.com",
    databaseURL: "https://ionic-chat-starter-9704d.firebaseio.com",
    projectId: "ionic-chat-starter-9704d",
    storageBucket: "ionic-chat-starter-9704d.appspot.com",
    messagingSenderId: "663911641399"
  };

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = IntroPage;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
    firebase.initializeApp(config);
  }
}



